let count = 0
let gpas = []
function createGps () {
  if (count === 60) {
    gpas = []
    count = 0
  } else {
    count += 1
  }
  let last = count === 0 ? null : gpas[gpas.length - 1]
  gpas.push({
    'latitude': (last ? last.latitude : 39.91071) + Random.integer(-20, 30) / 1000000,
    'longitude': (last ? last.longitude : 116.3187) + Random.integer(-20, 30) / 100000,
    'gpsTime': Date.now()
  })
  return {
    'code': 200,
    'message': 'operate successfully',
    'data': {
      'plateNo': 'PD6037', // 
      'routeNo': '001', // 
      'boardingTime': 1544140821000, // 
      'boardingTimeStr': '08:00:21', // 
      'unloadingTime': 1544168627000, // 
      'unloadingTimeStr': '15:43:47', // 
      'tripGps': gpas // 
    }
  }
}

