Mock.mock(`${ base_url }teacher/student/history/attendance`, 'post', req => {
  let data = qs.parse(req.body) // JSON.parse(req.body)
  let page = Number(data.page)
  let checkType = Number(data.checkType)
  if (page === 1) {
    today = Date.now()
  }
  let dayOffset = page - 1
  let array = []
  if (dayOffset <= 5) {
    let count = Number(data.pageSize)
    let hours = 24
    let time
    for (let i = 0; i < count; i++) {
      time = new Date(today)
      time.setHours(time.getHours() - dayOffset * count * hours - i * hours)
      array.push({
        'checkTimeStr': dateFtt('yyyy-MM-dd hh:mm:ss', time),
        'checkTime': time.getTime(),
        'isManual': checkType === 3 ? 0 : Random.integer(0, 1)
      })
    }
  }
  return {
    'code': 200,
    'message': 'operate successfully',
    'data': array
  }
})

