<template>
  <div class="l-form c-modify">
    <form-field :label="$t('setPwd.oldPwd')" :error="pwdError" v-model="oldPassword">
    </form-field>
    <form-field :label="$t('setPwd.newPwd')" :tip="$t('setPwd.tip')" :error="passwordErr" v-model="password">
    </form-field>
    <form-field :label="$t('common.confirm')" :error="confirmPasswordErr" v-model="confirmPassword">
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-pwd',
  components: {
    formField
  },
  data () {
    return {
      oldPassword: '',
      password: '',
      confirmPassword: '',
      passwordErr: '',
      confirmPasswordErr: '',
      pwdErrorInfo: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/password/modify' : 'parent/password/reset'
    },
    isValid () {
      return this.oldPassword && this.password && this.confirmPassword
    },
    pwdError () {
      return this.pwdErrorInfo ? this.$lang.code[this.pwdErrorInfo.code] || this.pwdErrorInfo.message : ''
    }
  },
  methods: {
    save () {
      if (!this.isValid) {
        return
      }
      this.pwdErrorInfo = null
      if (this.password.length < 6) {
        this.passwordErr = this.$t('pwdErr.tooShort')
        this.confirmPasswordErr = ''
        return
      } else if (this.password.length > 18) {
        this.passwordErr = this.$t('pwdErr.tooLong')
        this.confirmPasswordErr = ''
        return
      } else if (!/^[a-zA-Z\d~!@#$%^&*()_+`\-={}:";'<>?,.]+$/.test(this.password)) {
        this.passwordErr = this.$t('pwdErr.format')
        this.confirmPasswordErr = ''
        return
      } else if (/^[a-zA-Z]+$/.test(this.password) || /^\d+$/.test(this.password) || /^[~!@#$%^&*()_+`\-={}:";'<>?,.]+$/.test(this.password)) {
        // /(?=.*[a-zA-Z])(?=.*\d)(?=.*[~!@#$%^&*()_+`\-={}:";'<>?,.])[a-zA-Z\d~!@#$%^&*()_+`\-={}:";'<>?,.]/
        this.passwordErr = this.$t('pwdErr.simple')
        this.confirmPasswordErr = ''
        return
      } else {
        this.passwordErr = ''
      }
      if (this.confirmPassword !== this.password) {
        this.confirmPasswordErr = this.$t('pwdErr.notMatch')
        return
      } else {
        this.confirmPasswordErr = ''
      }
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            this.$router.back()
          },
          fail: error => {
            switch (error.code) {
              case RetCode.ERROR_PARENT_OLD_PWD_INCORRECT:
              case RetCode.ERROR_OLD_PWD_INCORRECT:
                this.pwdErrorInfo = {
                  code: error.code,
                  message: error.message
                }
                return true
              default:
                return false
            }
          }
        })
      }
      this.$httpPost.request({
        token: this.user.token,
        oldPwd: this.$md5(this.oldPassword),
        newPwd: this.$md5(this.password)
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
