import mock from './mock.js'

// vue.config.js
const useProxy = false

// mock
const useMockIntercept = false

// 852 90984154   123456
// 852 93144931   123456
// 852 93144931   123456
// 852 90280512   123456a
// 852 97272898   123456a
// 852 96672509   123456a
// davidcraig@cdnis.edu.hk   123456Q@   homeroom
// beatapatlewicz@cdnis.edu.hk   123456   homeroom
// donaldmacpherson@cdnis.edu.hk   123456a   admin
// timkaiser@cdnis.edu.hk   123456a   admin

let base_url = 'https://srdz.feixianggame.com/icapp/'

let eventCb = null

let bluetooth = 'false'

let mac = null

function delay (cb, result, time = 2000) {
  setTimeout(function () {
    cb(result)
  }, time)
}

function emit (type, data) {
  if (window[eventCb]) {
    window[eventCb](JSON.stringify({
      type,
      data
    }))
  }
}

function callHandler (funcName, data, cb) {
  switch (funcName) {
    case 'getNativeOS':
      cb('ios')
      break
    case 'getNativeAPPVer':
      cb('1.0.1')
      break
    case 'isBluetoothOpen':
      cb(bluetooth)
      break
    case 'openSysSetting':
      cb(bluetooth = 'true')
      emit(102)
      break
    case 'scanQRCode':
      delay(cb, JSON.stringify({
        type: 1,
        message: `G1MW99301825C#${ mock.random.string('upper', 12) }#42C89500`
      }))
      break
    case 'ringUp':
      log(`will call ${ data.phoneNumber }`)
      break
    case 'isBraceletConnected':
      delay(cb, JSON.stringify(mac ? {
        type: true,
        mac
      } : {
        type: false
      }))
      break
    case 'disConnectBracelet':
      mac = null
      delay(cb, 'true')
      break
    case 'connectBracelet':
      mac = data.mac
      delay(cb, 'true')
      break
    default:
      break
  }
}

setTimeout(function () {
  if (!window.JsNativeApi.isReady && window.WVJBCallbacks) {
    window.WVJBCallbacks[0]({
      registerHandler: function (callback) {
        eventCb = callback
      },
      callHandler
    })
  }
}, 5000)

if (useProxy) {
  base_url = '/'
}

if (useMockIntercept) {
  mock.addIntercept(base_url)
}

export default {
  log,
  base_url,
  timeout: 15000
}

let logElement = null
let toggle = false

export function log (str) {
  if (typeof window.console !== 'undefined' && console.log) {
    console.log(str)
  }
  if (!logElement) {
    let wrapperElement = document.createElement('div')
    wrapperElement.style.position = 'absolute'
    wrapperElement.style.top = '0px'
    wrapperElement.style.bottom = '0px'
    wrapperElement.style.left = '-60%'
    wrapperElement.style.width = '60%'
    wrapperElement.style.fontSize = '12px'
    wrapperElement.style.color = 'rgb(255, 0, 0)'
    wrapperElement.style.backgroundColor= 'rgba(0, 0, 0, 0.3)'
    logElement = document.createElement('div')
    logElement.style.position = 'absolute'
    logElement.style.top = '0px'
    logElement.style.bottom = '0px'
    logElement.style.left = '0px'
    logElement.style.right = '0px'
    logElement.style.overflow = 'auto'
    logElement.style.fontSize = '12px'
    logElement.style.wordBreak = 'break-all'
    logElement.style.backgroundColor = 'rgba(0, 0, 0, 0.3)'
    wrapperElement.appendChild(logElement)
    let toggleElement = document.createElement('div')
    toggleElement.style.position = 'absolute'
    toggleElement.style.bottom = '0px'
    toggleElement.style.right = '-30px'
    toggleElement.style.width = '30px'
    toggleElement.style.height = '30px'
    toggleElement.style.backgroundColor = 'rgb(255, 0, 0)'
    toggleElement.style.borderTopRightRadius = '100%'
    toggleElement.onclick = function () {
      toggle = !toggle
      wrapperElement.style.left = toggle ? '0px' : '-60%'
    }
    wrapperElement.appendChild(toggleElement)
    document.body.appendChild(wrapperElement)
  }
  let element = document.createElement('div')
  element.innerHTML = `${ str }</br>----------`
  logElement.insertBefore(element, logElement.childNodes[0])
}
